<template>
  <v-container id="wrap" @click="create()">
    <v-layout
      column
      align-center
    >
      <h1> click background</h1>
    </v-layout>
  </v-container>
</template>

<script>
import $ from 'jquery';
export default {
  data() {
    return {
    };
  },
  computed: {
  },
  methods: {
    create(){
      $('#wrap').append('<div class="box"></div>');
    }
  },
  watch:{
  }
}
</script>

<style>
#wrap{
  height: 100vh;
  width: 100%;
}
.box{
  display: inline-block;
  background-color: pink;
  width: 200px;
  height: 200px;
  position: relative;

  animation-name: myframes;
  animation-duration: 300ms;
  animation-timing-function: ease-in-out;
  animation-delay: 0s;
  animation-iteration-count: 1;
  animation-direction: normal;
  animation-fill-mode: forwards;
}

@keyframes myframes {
  0%{
    transform: scale(0) 
  }
  100%{
    transform: scale(1)
  }
}


</style>
